import React, { useState } from 'react';
import "./SearchBlock.scss";
import PlacesAutocomplete from 'react-places-autocomplete';
import Loader from '../../common/Loader/Loader';
import AirInfo from '../AirInfo/AirInfo';
import { motion } from "framer-motion"



export default function SearchBlock({ isLoaded, setIsLoaded, map, address, handleSelect, latitude, longitude, getAirInfo, setAddress, setUserLocation, airInfo }) {


  const searchOptions = {
    options: 3,
  }


  const [isPressed, setIsPressed] = useState(false)
  return (
    <div className="search-block">
      {isPressed ? <AirInfo isLoaded={isLoaded} setIsLoaded={setIsLoaded} isPressed={isPressed} setIsPressed={setIsPressed} airInfo={airInfo} /> : (<motion.div animate={{ bottom: 0 }} initial={{ bottom: -100 }} className="search-block__row">
        <PlacesAutocomplete searchOptions={searchOptions} value={address} onChange={setAddress} onSelect={handleSelect} >
          {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => {
            return (
              <div className="search-block__box input-box">
                <input value={address} className="input-box__input" {...getInputProps({ placeholder: 'I want to search for...' })} />
                <div style={{ borderRadius: "20px" }} className="input-box__suggestions">
                  {loading ? <div><Loader /></div> : null}
                  {suggestions.map((suggestion) => {
                    const style = {
                      backgroundColor: suggestion.active ? "#8691b9" : "#DDE6F1",
                      padding: "10px",
                    }
                    return <div className="input-box__suggestion" key={suggestion.description} {...getSuggestionItemProps(suggestion, { style })}>{suggestion.description}</div>
                  })}
                </div>
              </div>
            )
          }}
        </PlacesAutocomplete>
        <div className="search-block__buttons">
          <button className="search-block__btn" onClick={() => {
            getAirInfo();
            setIsPressed(!isPressed)
          }}> Check</button>
        </div>
      </motion.div>)}
    </div>
  )
}
